export default "<script context=\"module\">\n  const example = \"CustomEventsGlider\"\n</script>\n\n<script lang=\"ts\">\n  import Glider  from '..'\n  import Details from './lib/Details.svelte'\n\n  let message: string | null = null\n  let timer:   number = 0\n\n  const MSG_TIMEOUT: number = 5000\n\n  const updateMessage = (subject: string, value: any) => {\n    if (timer > 0) {\n      clearTimeout(timer)\n      timer = 0\n    }\n    message = subject + ((value || (value === 0)) ? `: ${JSON.stringify(value, null, 2)}` : '')\n    timer   = setTimeout(() => {message = null}, MSG_TIMEOUT)\n  }\n</script>\n\n<style>\n  div.container > h5 {text-align: left; color: #f96743;}\n</style>\n\n<div class=\"container\">\n  {#if message}\n    <h5>{message}</h5>\n  {/if}\n  <Glider\n    class=\"glider-container\"\n    draggable\n    hasDots\n    slidesToShow={3}\n    onLoad={        (e) => {updateMessage('Loaded',        [e.detail.target.tagName, ...e.detail.target.classList].join('.'))}}\n    onSlideVisible={(e) => {updateMessage('Slide Visible', e.detail.slide)}}\n    onSlideHidden={ (e) => {updateMessage('Slide Hidden',  e.detail.slide)}}\n    onRefresh={     ( ) => {updateMessage('Refresh')}}\n  >\n    <div class=\"slide\">\n      <span>1</span>\n    </div>\n    <div class=\"slide\">\n      <span>2</span>\n    </div>\n    <div class=\"slide\">\n      <span>3</span>\n    </div>\n    <div class=\"slide\">\n      <span>4</span>\n    </div>\n    <div class=\"slide\">\n      <span>5</span>\n    </div>\n    <div class=\"slide\">\n      <span>6</span>\n    </div>\n    <div class=\"slide\">\n      <span>7</span>\n    </div>\n    <div class=\"slide\">\n      <span>8</span>\n    </div>\n    <div class=\"slide\">\n      <span>9</span>\n    </div>\n    <div class=\"slide\">\n      <span>10</span>\n    </div>\n    <div class=\"slide\">\n      <span>11</span>\n    </div>\n    <div class=\"slide\">\n      <span>12</span>\n    </div>\n  </Glider>\n  <Details {example} />\n</div>\n"